import { mapActions } from 'vuex'
export default {
  name: 'music-list',
  data() {
    return {}
  },
  props: {
    preIndex: {
      type: Number,
      default: 0
    },
    limit: {
      type: Number,
      default: 0
    },
    list: {
      type: Array,
      default: () => []
    }
  },
  mounted() {
    const height = $('.list-item').height()
    this.$emit('hasHeight', height)
  },
  methods: {
    ...mapActions(['selectPlay']),
    
    keys({ id }) {
      return id
       
    },
    async selectItem(item, index) {
    
      this.selectPlay({ list: this.__cloneDeep__(this.list), index })
    }
  }
}



