import { mapGetters, mapMutations, mapActions } from 'vuex'
import mixin from './mixin.js'
import playMode from '@/common/js/config'
export default {
  name: '',
  mixins: [mixin],
  props: {
    playlist1: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      showFlag: false
    }
  },
  computed: {
    wrapperStyle() {
      return {
        maxHeight: window.innerHeight / 1.6 + 'px'
      }
    },
    modeText() {
      return this.mode === playMode.loop
        ? '循环播放'
        : this.mode === playMode.random
        ? '随机播放'
        : '顺序播放'
    }
  },
  methods: {
    ...mapMutations({
      setCurrentIndex: 'SET_CURRENT_INDEX'
    }),
    ...mapActions(['deleteSong', 'deleteSongList']),

    hide() {
      this.showFlag = false
    },
    show() {
      this.showFlag = true

      this.$nextTick(() => {
        this.$refs.listContent.refresh()
        this.scrollToCurrent()
      })
    },
    clearList() {
      try {
        this.MessageBox.confirm('确定清空列表?').then(action => {
          this.deleteSongList()
          this.hide()
        })
      } catch (err) {
        console.log(err)
      }
    },
    deleteOne(item) {
      this.deleteSong(item)
      if (!this.playlist.length) {
        this.hide()
      }
    },
    playItem(item, index) {
      if (this.mode === playMode.random) {
        index = this.playlist.findIndex(song => item.id === song.id)
      }
      this.setCurrentIndex(index)
    },
    scrollToCurrent() {
      const index = this.sequenceList.findIndex(
        item => item.id === this.currentSong.id
      )
      this.$refs.listContent.scrollToElement(this.$refs.songItem[index], 300)
    }
  },
  watch: {
    currentSong(newSong, oldSong) {
      if (newSong.id === oldSong.id || newSong.id === undefined) {
        return
      }
      this.$nextTick(this.scrollToCurrent)
    }
  }
}



