export default {
  name: 'mv-list',
  data() {
    return {
      currentMv: null,
      playing: false
    };
  },
  computed: {
    keys() {
      return {

        picUrl: 'picUrl',
        title: 'title',
        desc: 'desc',
        ...this.dataKey
      }
    },
  },
  props: {
    data: {
      type: Array,
      default: () => ([])
    },
    twoColumn: {
      type: Boolean,
      default: false
    },
    useBackground: {
      type: Boolean,
      default: false
    },
    dataKey: {
      type: Object,
      default: () => ({})
    }

  },
  created() {
    // this.$emit('on-key', this.keys)
    this.$Lazyload.$on('loaded', function(el, formCache) {
      // console.log(el, formCache)
    })
  },
  mounted() {
    // this.video=this.$refs.video
  },
  methods: {
    selectPlay(item) {
      this.$router.push({ name: 'mvPlayer', query: item })
    },
    oncanplay() {
      this.playing = true
      // this.
    },
    playItem(e, mv) {
      // console.log(e.target, url)
      // e.target.src = mv.url
      e.target.autoplay = true
      e.target.controls = true
      this.currentMv = mv;
    },
  }
};




