const radios = [
  { text: '标准', value: 48 },
  { text: '高清', value: 96 },
  { text: '高品质', value: 320 },
  { text: '无损音质', value: 'flac' },
  { text: '压缩无损音质', value: 'ape' }
]
export default {
  name: '',
  data() {
    return {
      muted: false,
      radios,
      showFlag: false,
      volume: 20
    };
  },
  props: {
    radio: {
      type: [Number, String],
      default: 96
    },
    muted1: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    setTimeout(() => {

      this.$emit('input', this.volume)
    })
  },
  computed: {
    ...Vuex.mapGetters(['currentSong']),
    volumeCls() {
      return this.volume === 0 || this.muted ? 'cubeic-mute' : 'cubeic-volume'
    },
  },
  watch: {
    volume(newV, oldV) {
      this.oldV = oldV
    },
  },
  methods: {
    show() {
      this.showFlag = true
    },
    hide() {
      this.showFlag = false
    },
    toggleRadio(item) {
      if (this.radio === item.value) {
        return
      }
      if (!this.currentSong.hasOwnProperty('time')) {
        this.Toast({
          message: '此音质不可用',
          duration: 1000
        })
        return
      }
      // this.brVal = this.radios.find(_item => item.value === _item.value).value;
      this.$emit('toggleRadio', item)
      // this.$emit('update:radio', item.value)
      this.hide()
      this.showToast = this.Toast({
        message: '切换为' + item.text,
        duration: 2000
      })
    },
    muteVolume() {
      this.muted = !this.muted;
      this.$emit('operate', this.muted)
    }
  }
};




