export default {
  data() {
    return {
      message: '正在加载...',
    }
  },
  props: {
    error: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    error(newError) {
      this.message = newError ? "加载错误" :
        "正在加载..."
    }
  }
};




